/* default */
html, body {width:100%; height:100%;}
html {font-size:62.5%;}
body {font-family:'Noto Sans KR', sans-serif; font-size:14px; line-height:1.5; letter-spacing:-0.035em; -webkit-font-smoothing:antialiased; -webkit-text-size-adjust:none; -moz-osx-font-smoothing:grayscale; /*-webkit-overflow-scrolling:touch; -webkit-user-select:none; -khtml-user-select:none; -moz-user-select:none; -ms-user-select:none; user-select:none; -webkit-tap-highlight-color:rgba(0, 0, 0, 0); -webkit-tap-highlight-color:transparent; */;}
body:before {content:''; position:fixed; left:0; top:0; width:100%; height:0;}/* fix webkit bug(scroll event delay) */
body, iframe, div, p, span, strong, em, b, i,  
h1, h2, h3, h4, h5, h6, 
form, fieldset, button, a, label, 
table, tbody, thead, tr, th, td{margin:0; padding:0; box-sizing:border-box; font:inherit;}
dl, dt, dd, ul, ol, li {box-sizing:border-box; list-style:none; padding:0; margin:0; font:inherit;}
img, fieldset, iframe, button {border:0 none;}
table { width: 100%; table-layout: fixed; border-collapse: collapse; border-spacing:0;}
th, td {border-collapse:collapse;}
hr {border:0; border-top:1px solid #ccc; margin:40px 0;}
em, u, cite, i, abbr {font:inherit; text-decoration:none;}
strong, b {font:inherit;}
a {font:inherit; color:inherit; outline-offset:1px;}
button {border:0 none; font:inherit; cursor:pointer; background:transparent; color:inherit;}
input, textarea, select {box-sizing:border-box; font:inherit, sans-serif; outline:0;}
label{cursor:pointer;}

caption{position:relative !important; width:1px !important; height:1px !important; color:transparent !important; overflow:hidden !important; clip:rect(1px, 1px,1px, 1px); clip-path:inset(50%); margin:-1px 0 0;}
legend, .hidden, .hide { position:absolute !important; width:1px !important; height:1px !important; color:transparent !important; overflow:hidden !important; clip:rect(1px, 1px,1px, 1px); clip-path:inset(50%); }
/* html5 */
article, aside, details, figcaption, figure, footer, header, main, hgroup, nav, section, summary {display:block; margin:0; padding:0; box-sizing:border-box;}
caption *{display:inline-block !important}
