/* [base] FORM ---------------------------------------------------------------------------*/
/* style init */
textarea,
input{ -webkit-appearance: none; -webkit-border-radius: 0;}
/* Removes the clear button from date inputs */
input[type="date"]::-webkit-clear-button {display: none;}
input[type="date"]::-webkit-inner-spin-button { display: none;}
input[type="date"]::-webkit-calendar-picker-indicator {display: none;}
input[type="date"] {appearance: none; -webkit-appearance: none;}
/* radio & checkbox */
input[type="radio"],
input[type="checkbox"] {opacity:0; filter:alpha(opacity=0); display:inline-block; position:absolute; z-index:-1; vertical-align:top; appearance:none; -moz-appearance:none; -webkit-appearance:none; margin:0;}
.lb-radio,
.lb-check {position:relative; display:inline-flex; vertical-align:top; align-items:center; min-height:40px; font-size:13px; padding:0; margin:0; cursor:pointer; line-height:1;}
.lb-radio:before,
.lb-check:before {content:""; display:block; flex:0; min-width:20px; min-height:20px; margin-right:5px; box-sizing:border-box; border:2px solid #333; transition:box-shadow 0.5s, border 0.3s;}
.lb-radio:before {border-radius:50%;}
.lb-check:before {border-radius:3px;}
:checked + .lb-radio:before,
:checked + .lb-check:before,
.lb-radio.checked:before,
.lb-check.checked:before {border:6px solid #333;}
.lb-radio.activated:before,
.lb-check.activated:before,
.lb-radio:hover:before,
.lb-check:hover:before,
.lb-radio:focus:before,
.lb-check:focus:before {box-shadow:0 0 5px rgba(0,0,0,0.7);}
:checked + .lb-radio,
:checked + .lb-check,
.lb-radio.checked,
.lb-check.checked {color:#000;}
:focus + .lb-radio,
:focus + .lb-check{text-decoration:underline;}
:disabled + .lb-radio,
:disabled + .lb-check,
.lb-radio.disabled,
.lb-check.disabled {opacity:0.3; filter:alpha(opacity=30); cursor:not-allowed;}
.lb-radio span,
.lb-check span {display:inline-block; vertical-align:middle; height:100%; border-bottom:1px solid transparent;}
.lb-check.all {font-size:16px; color:#444;}
.lb-check.all span {padding-top:1px;}
.lb-tit {display:inline-block; height:40px; margin-right:10px; padding-top:10px; vertical-align:top; cursor:pointer;}
/* input */
.inp-base {display:inline-block; vertical-align:top; width:100%; border:1px solid #c9c9c9; border-radius:2px; height:40px; padding:0 10px; outline:0; margin:0;}
.inp-base:focus{border-color:#000;}
.inp-base::-webkit-search-cancel-button{display:none;}
textarea.inp-base{padding:10px; height:100px;}
/* select */
select {display:inline-block; position:relative; vertical-align:top; width:auto; height:40px; padding:0 40px 0 10px; border-radius:2px; 
background:#fff; appearance:none; -moz-appearance:none; -webkit-appearance:none; border:1px solid #c9c9c9; font-size:14px; cursor:pointer;}
select::-ms-expand {display:none;}
select optgroup,
select option {font-style:normal; background-color:#fff; font-size:13px;}
select[disabled] {background-color:#f7f7f7; color:#888;}
select:focus{border-color:#000;}
.ie9 select {padding:0;}



/* [base] BUTTON ---------------------------------------------------------------------------*/
/* button wrap */
.btn-wrap {position:relative; margin:10px 0; width:100%; text-align:center; font-size:0;}
.btn-wrap [class*="btn-base"]{ margin:5px;}
.btn-wrap.txta-l [class*="btn-base"]{margin-left:0; margin-right:10px;}
.btn-wrap.txta-r [class*="btn-base"]{margin-right:0; margin-left:10px;}
/* button */
[class*="btn-base"]{display:inline-block; padding:0 20px; min-height:40px; min-width:60px; border:1px solid #63686d; font-size:14px; text-align:center; background:#fff; vertical-align:top; color:#222; text-decoration:none; line-height:1;}
div[class*="btn-base"],
span[class*="btn-base"],
a[class*="btn-base"]{padding:12px 20px 8px;}
[class*="btn-base"] span{vertical-align:middle; display:inline-block; line-height:1; margin-top:-3px;}
[class*="btn-base"]:hover,
[class*="btn-base"]:focus{border:1px solid #000; background:#eee;}
[class*="btn-base"].disabled{opacity:0.7; filter:alpha(opacity=70); border:1px solid #ccc; box-shadow:none; cursor:default;}
[class*="btn-base"].disabled:hover,
[class*="btn-base"].disabled:focus{border-color:#ccc; background:inherit;}
/* base size */
.btn-base-s{min-height:30px;} 
a.btn-base-s{padding:7px 20px 3px;}
.btn-base-m{min-height:40px;} 
.btn-base-l{min-height:60px; font-size:16px;}
a.btn-base-l{padding:21px 20px 16px;}
/* button icon style */
[class*="btn-base"].ico-print:after {content:"print"; font-family:'Material Icons'; font-feature-settings:'liga'; display:inline-block; width:20px; height:20px; margin-left:5px; vertical-align:middle; margin-top:-3px; font-size:20px;}
/* X close, del button, menu ... */
.btn-today,
.btn-del,
.btn-close {position:relative; width:20px; height:20px; overflow:hidden; color:transparent; font-size:xx-small;}
.btn-del:after,
.btn-close:after{content:"close"; font-family:'Material Icons'; font-feature-settings:'liga'; display:block; width:100%; height:100%; font-size:20px; color:#fff; font-weight: 900; line-height:1;}
.btn-del:after {color:#222;}
.btn-today:after{content:"today"; font-family:'Material Icons'; font-feature-settings:'liga'; display:block; width:100%; height:100%; font-size:20px; color:#000; font-weight: 900; line-height:1;}


/* [base] BOX ---------------------------------------------------------------------------*/
.box-base {position:relative; width:100%; margin:10px 0; padding:10px 20px; background:#fff; border:1px solid rgb(180, 180, 180);}


/* [base] TABLE ---------------------------------------------------------------------------*/
/* col width */
.col-th {width:100px;}
.col-num {width:50px;}
.col-name {width:100px;}
.col-date {width:120px;}
.col-date-scr {width:137px;}
.col-id {width:100px;}
.col-auto {width:auto;}
.col-xxxs {width:10%;}
.col-xxs {width:10%;}
.col-xs {width:15%;}
.col-s {width:20%;}
.col-m {width:25%;}
.col-l {width:30%;}
.col-xl {width:35%;}
.col-xxl {width:40%;}
.col-xxxl {width:45%;}
.col-half {width:50%;}
/* table */
.tbl-base {margin:0; width:100%; position:relative; border-top:1px solid #c5c5c5; border-bottom:1px solid #c5c5c5;}
.tbl-base table{width:100%;}
.tbl-base table > thead th {background:#f1f1f1; border-bottom:1px solid #e4e4e4; border-left:1px solid #e4e4e4; color:#000; padding:5px; font-weight:500; font-size:14px; height:40px;}
.tbl-base table > tbody > tr > th,
.tbl-base table > tbody > tr > td {border-top:1px solid #e4e4e4; border-left:1px solid #e4e4e4; color:#444; padding:5px; font-weight:300; font-size:14px; word-break:break-all; background:#fff;}
.tbl-base table > tbody > tr:first-child > th,
.tbl-base table > tbody > tr:first-child > td {border-top:0;}
.tbl-base table > thead > tr > th:first-child,
.tbl-base table > tbody > tr > th:first-child,
.tbl-base table > tbody > tr > td:first-child {border-left:0;}
.tbl-base table > tbody > tr > th {background:#f1f1f1; color:#000; font-weight:500; font-size:14px;}
.tbl-base tbody + tbody,
.tbl-base tbody.line-t{border-top:1px solid #c5c5c5;}


/* [base] BULLET LIST ---------------------------------------------------------------------------*/
.bul-hyphen,
.bul-dot,
.bul-etc {margin:10px 0;}
.bul-dot li,
.bul-dot > div > dd,
.bul-dot > dd,
.bul-hyphen li,
.bul-hyphen > div > dd,
.bul-hyphen > dd,
p.bul-dot,
p.bul-hyphen,
p.bul-star,
p.bul-care,
p.bul-etc {position:relative; margin-top:5px; padding-left:10px; line-height:1.5; display:block;}
p.bul-care {padding-left:15px;}
p.bul-etc {padding-left:20px;}
.bul-dot li li:first-child,
.bul-dot li:first-child,
.bul-hyphen li li:first-child,
.bul-hyphen li:first-child {margin-top:0;}
.bul-dot li:before,
.bul-dot dd:before,
p.bul-dot:before {content:"·"; position:absolute; left:0; top:2px; font-weight:900; line-height:1.2;}
.bul-hyphen li:before,
.bul-hyphen dd:before,
p.bul-hyphen:before {content:"-"; position:absolute; left:0; top:1px; font-weight:400; line-height:1.2;}
p.bul-star:before {content:"*"; position:absolute; left:0; top:6px; font-weight:400; line-height:1.2;}
p.bul-care:before {content:"※"; position:absolute; left:0; top:2px; font-weight:400; line-height:1.2;}
.bul-hyphen ul,
.bul-dot ul,
.bul-etc ul,
.bul-etc ol {margin:5px 0 0;}
.bul-dot dd li,
.bul-dot li li,
.bul-hyphen dd li,
.bul-hyphen li li {margin:5px 0 0; padding-left:10px;}
.bul-dot dt,
.bul-hyphen dt {color:#000; font-weight:500;}
.bul-dot ul li:before{content:"-"; position:absolute; left:0; top:0; font-weight:400; line-height:1.2;}
.bul-hyphen ul li:before{content:"·"; position:absolute; left:0; top:2px; font-weight:900; line-height:1.2;}
.bul-etc > li {position:relative; margin-top:5px; padding-left:20px; line-height:1.5; display:block;}
.bul-ico{position:relative; top:0; margin:0 0 0 -20px; width:20px; display:inline-block;}
dl.bul-dot > div{margin-top:15px}
dl.bul-dot > div:first-child{margin-top:0;}
dl.bul-hyphen > div{margin-top:15px}
dl.bul-hyphen > div:first-child{margin-top:0;}


/* [base] Layout ---------------------------------------------------------------------------*/
/* layout inline-block */
.in-wrap{position:relative; font-size:0; margin:10px 0 0;}
.in-wrap > *,
.in-item{font-size:14px; display:inline-block; vertical-align:top; margin-top:10px;}
.in-item > * {vertical-align:top;}
/* layout float */
.fl-wrap{position:relative; margin:10px 0;}
.fl-wrap:after{content:""; display:block; clear:both;}
.fl-item-l{float:left; position:relative;}
.fl-item-r{float:right; position:relative;}
/* layout flex */
.fx-wrap{position:relative; display:-webkit-box; display:-ms-flexbox; display:flex; -webkit-box-lines:single; -ms-flex-wrap:nowrap; flex-wrap:nowrap; margin:10px 0;}
.fx-dir-row{-webkit-box-orient:horizontal; -ms-flex-direction:row; flex-direction:row;}
.fx-dir-rev{-webkit-box-orient:horizontal; -webkit-box-direction:reverse; -ms-flex-direction:row-reverse; flex-direction:row-reverse;}
.fx-dir-col{-webkit-box-orient:vertical; -ms-flex-direction:column; flex-direction:column;}
.fx-dir-revcol{-webkit-box-orient:vertical; -webkit-box-direction:reverse; -ms-flex-direction:column-reverse; flex-direction:column-reverse;}
.fx-line{-webkit-box-lines:multiple; -ms-flex-wrap:wrap; flex-wrap:wrap;}
.fx-align-start{-webkit-box-pack:start; -ms-flex-pack:start; justify-content:flex-start;}
.fx-align-end{-webkit-box-pack:end; -ms-flex-pack:end; justify-content:flex-end;}
.fx-align-center{-webkit-box-pack:center; -ms-flex-pack:center; justify-content:center;}
.fx-align-justify{-webkit-box-pack:justify; -ms-flex-pack:justify; justify-content:space-between;}
.fx-valign-start{-webkit-box-align:start; -ms-flex-align:start; align-items:flex-start;}
.fx-valign-end{-webkit-box-align:end; -ms-flex-align:end; align-items:flex-end;}
.fx-valign-center{-webkit-box-align:center; -ms-flex-align:center; align-items:center;}
.fx-valign-stretch{-webkit-box-align:stretch; -ms-flex-align:stretch; align-items:stretch;}
.fx-valign-baseline{-webkit-box-align:baseline; -ms-flex-align:baseline; align-items:baseline;}
.fx-order-1{-webkit-box-ordinal-group:1; -ms-flex-order:1; order:1;}
.fx-order-2{-webkit-box-ordinal-group:2; -ms-flex-order:2; order:2;}
.fx-order-3{-webkit-box-ordinal-group:3; -ms-flex-order:3; order:3;}
.fx-order-4{-webkit-box-ordinal-group:4; -ms-flex-order:4; order:4;}
.fx-order-5{-webkit-box-ordinal-group:5; -ms-flex-order:5; order:5;}
.fx-item-0{-webkit-box-flex:0; -ms-flex:0; flex-grow:0;}
